@import "../../global.scss";
.resumeList{
    font-size               : 15px;
    margin-right            : 50px;
    padding                 : 7px;
    border-radius           : 10px;
    cursor                  : pointer;
    overflow                : hidden;
    @include mobile{
        margin-right        : 20px;
    }
    &.active{
        background-color    : $mainColor;
        color               : white;
        overflow            : hidden;
    }
}